import { ElementNode, For, Text, View, hexColor } from "@lightningjs/solid";
import { Column, Row } from "@lightningjs/solid-primitives";
import { keyBoards } from "../../keyBoards";
import { 
  KeyboardChooseStyle,
  KeyboardItemNormalStyle,
  KeyboardItemSpecalIconStyle,
  KeyboardItemSpecalStyle,
  KeyboardItemStyle,
  KeyboardMainStyle,
  KeyboardRowStyle,
  KeyboardTextStyle,
  backdropKeyboardStyle,
  inputSearchStyle,
  keyboardContainerStyle,
  noDataStyle,
  resultSearchStyle,
  resultVideoItemStyle,
  resultVideosStyle,
  spaceSearchStyle
} from "../../assets/styles/keyboardStyle";
import { Show, createEffect, createMemo, createSelector, createSignal, on } from "solid-js";
import { TileRow } from "../components";
import { createInfiniteScroll } from "../components/pagination";
import { setFocusMenu } from "../state";
const basePath = import.meta.env.BASE_URL;
import searchProvider from '../api/providers/search';

export default function Search(props) {
    let backdropKeyboardRef, inputSearchRef, resultSearchRef, keyboardContainerRef, keyboardMainRef, resultVideosRef, keyboardChooseRef, resultVideosMainRef, KeyboardItemSpecalRef;
    const [searchValue, setSearchValue] = createSignal('');
    const [columnY, setcolumnY] = createSignal(0);

    let searchResultProvider = createMemo(() => {
      return createInfiniteScroll(searchProvider(5, searchValue()));
    });

    createEffect(on(searchValue, (value) => {
      setcolumnY(0);
      searchResultProvider().setPage(1);
    }));

    const isFirst = createSelector(() => {
      return 0;
    });
  
    function onFocus(this: ElementNode) {
      this.states.add('focus');
    };
  
    function onBlur(this: ElementNode) {
      this.states.remove('focus');
    };

    const selectKeySpace = () => {
      return () => {
        setSearchValue(prev => prev + ' ')
      }
    };

    const selectKeyBackSpace = () => {
      return () => {
        setSearchValue(prev => prev.slice(0, -1))
      }
    };

    function selectKeyBoard(key: string) {
      return () => {
        setSearchValue(prev => prev + key)
      }
    };

    const renderKeyboard = () => {
      let data :any = []
      const elementsPerDiv = 6;
      for (let i = 0; i < keyBoards.length; i += elementsPerDiv) {
        const divElements = keyBoards.slice(i, i + elementsPerDiv);
        data.push(divElements);
      }
  
      return (
        <For each={data}>
          {(item, i) =>
            <Row style={KeyboardRowStyle} autofocus={false} plinko>
              <For each={item}>
                {(key, j) => (
                  <View
                    style={KeyboardItemStyle}
                    onEnter={selectKeyBoard(key)}
                    animate
                    onBlur={onBlur}
                    onFocus={onFocus}
                    forwardStates
                    autofocus={false}
                  >
                    <Text style={KeyboardTextStyle}>
                      {key}
                    </Text>
                  </View>
                ) }
              </For>
            </Row>
          }
        </For> 
      )
    }

    function onRowFocus(this: ElementNode) {
      this.children.selected?.setFocus();
      setcolumnY((this.y || 0) * -1 + 24);
      let numPages = searchResultProvider().pages().length;
      if(this.parent){
        if (numPages === 0 || this.parent.selected && this.parent.selected >= numPages - 2) {
          searchResultProvider().setPage(p => p + 1);
        }
      }
    }

    function onEnter(this: ElementNode) {
    };
  
    return (
      <View ref={backdropKeyboardRef} animate style={backdropKeyboardStyle} autofocus>
        <View
          ref={keyboardContainerRef}
          animate
          style={keyboardContainerStyle}
          autofocus
          plinko
        >
          <View ref={inputSearchRef} style={spaceSearchStyle} skipFocus></View>
          <View ref={inputSearchRef} style={inputSearchStyle} animate skipFocus>
            <Text style={{
              color: hexColor('#ffffff'),
              fontSize: 30,
              marginLeft: 20,
            }}>
              { searchValue() }
            </Text>
          </View>
          <Column
            style={KeyboardMainStyle}
            ref={keyboardMainRef}
            autofocus
            onDown={() => {
              if(keyboardChooseRef.states.has('focus')){
                return false;
              }
            }}
            plinko
          >
            <Column
              style={KeyboardChooseStyle}
              ref={keyboardChooseRef}
              onRight={() => {
                resultVideosRef.setFocus();
                if(searchResultProvider().pages().length > 0) {
                  resultVideosMainRef.setFocus();
                }
              }}
              onLeft={() => {
                setFocusMenu(true);
              }}
              >
              <Row style={KeyboardRowStyle} autofocus={false}>
                <View
                  style={KeyboardItemSpecalStyle}
                  ref={KeyboardItemSpecalRef}
                  onEnter={selectKeySpace()}
                  animate
                  autofocus={false}
                >
                  <View
                    style={KeyboardItemSpecalIconStyle}
                    src={basePath + 'assets/icon-space.png'}
                    forwardStates
                    animate
                  ></View>
                </View>
                <View
                  style={KeyboardItemSpecalStyle}
                  onEnter={selectKeyBackSpace()}
                  animate
                  autofocus={false}
                >
                  <View
                    style={KeyboardItemSpecalIconStyle}
                    src={basePath + 'assets/icon-back-space.png'}
                    forwardStates
                    animate
                  ></View>
                </View>
              </Row>
              <Column plinko style={KeyboardItemNormalStyle}>
                {
                  renderKeyboard()
                }
              </Column>
              <Column
                ref={resultSearchRef}
                animate
                style={resultSearchStyle}
                forwardStates
              >
                <Text style={{
                  color: hexColor('#504d4d'),
                  fontSize: 30,
                  focus: {
                    color: hexColor('#ffffff'),
                  }
                }}>
                  {/* Result Search */}
                </Text>
              </Column>
            </Column>
            <View
              style={resultVideosStyle}
              ref={resultVideosRef}
              onLeft={() => {
                keyboardChooseRef.setFocus();
              }}
            >
              <Show when={searchResultProvider().pages().length == 0}>
                <View style={noDataStyle}>
                  <Text
                    style={{
                      fontSize: 40,
                      display: 'flex',
                      justifyContent: 'center',
                      textAlign: 'center'
                    }}
                  >
                    No Data
                  </Text>
                </View>
              </Show>
              <Show when={searchResultProvider().pages().length > 0}>
                <Column ref={resultVideosMainRef} animate y={columnY()} style={resultVideoItemStyle} plinko>
                  <For each={searchResultProvider().pages()}>
                    {(items, i) =>
                      <TileRow autofocus={isFirst(i()) && resultVideosRef.states.has('focus')} items={items} onFocus={onRowFocus} onEnter={onEnter} />
                    }
                  </For>
                </Column>
              </Show>
            </View>
          </Column>
        </View>
      </View>
    );
  }
  