import { type AnimationSettings } from "@lightningjs/renderer";
import { homeBackgroundState } from "../state.js";
import { type IntrinsicNodeStyleProps, View, hexColor } from "@lightningjs/solid";
import { createEffect, on } from "solid-js";
import theme from 'theme';

export default function HomeBackground() {
  let bg1, bg2;
  let active = 0;
  const alpha = 1;
  const animationSettings = { duration: 1000, easing: 'ease-in-out' } satisfies Partial<AnimationSettings>;
  const bgStyles = { alpha, color: 0xffffffff, transition: {
    alpha: animationSettings
  } } satisfies IntrinsicNodeStyleProps;

  createEffect(on(homeBackgroundState, (img: string) => {
    if (img.startsWith('#')) {
      bg1.color = img;
      bg1.src = '';
      bg1.alpha = 1;
      active = 1;
      bg2.alpha = 0;
      return;
    } else {
      bg1.color = 0xffffffff;
    }

    if (active === 0) {
      bg1.src = img;
      active = 1;
      return;
    }

    if (active === 1) {
      bg2.src = img;
      active = 2;
      bg1.alpha = 0;
      bg2.alpha = 1;
      return;
    }

    if (active === 2) {
      bg1.src = img;
      active = 1;
      bg1.alpha = alpha;
      bg2.alpha = 0;
    }
  }, { defer: true}))

  return (<>
  <View width={window.devicePixelRatio > 1 ? 1920 : window.innerWidth} height={window.devicePixelRatio > 1 ? 1080 : window.innerHeight} zIndex={-5} x={-21}>
    <View ref={bg1} style={bgStyles} animate/>
    <View ref={bg2} style={bgStyles} alpha={0} animate/>
    <View
      animate
      effects={{
        radialGradient: {
          colors: [0x0000007C, 0x0000007C, hexColor("#000000")],
          stops: [0.6, 0.1, 1.2],
          height: 500,
          width: (window.devicePixelRatio > 1 ? 1920 : window.innerWidth) - 300,
          pivot: [0.8, 0], 
        },
      }}
    />
    </View>
  </>);
}
