import { IntrinsicNodeStyleProps, deg2rad, hexColor } from "@lightningjs/solid";
import theme from "theme";

export default {
  Column: {
    ...(theme.flexColumn as IntrinsicNodeStyleProps),
    width: 320,
    height: 600,
    y: 220,
    gap: 60,
    zIndex: 101,
    colors: hexColor(theme.color.primary),
    x: [
      20,
      {
        duration: 250,
        easing: "ease-in-out",
      },
    ] as any, 
  } satisfies IntrinsicNodeStyleProps,
  menuLineStyle: {
    width: 50,
    height: 0,
    x: 0,
    y: 58,
    selected: {
      height: 6,
      color: hexColor('#ff0000'),
    }
  } satisfies IntrinsicNodeStyleProps,
  MenuWrapper: {
    zIndex: 1000,
    // angle: deg2rad(270),
    // stops: [0, 0.4, 0.8],
    width: 100,
    height: window.devicePixelRatio > 1 ? 1080 : window.innerHeight,
    color: hexColor(theme.color.primary),
    active: {
      width: [
        380,
        {
          duration: 250,
          easing: 'ease-in-out',
          alpha: 1,
        },
      ] as any,
    },
    unactive: {
      width: [
        100,
        {
          duration: 250,
          easing: 'ease-in-out',
        },
      ] as any,
    },
  } satisfies IntrinsicNodeStyleProps,
  MenuText: {
    zIndex: 1,
    fontSize: 30,
    x: 86,
    y: 10,
    height: 50,
    alpha: 0,
    color: hexColor("#5b5b5b"),
    scale: 1,
    lineHeight: 1,
    focus: {
      scale: [
        1.1,
        {
          duration: 250,
          easing: 'ease-in-out',
        },
      ],
      color: hexColor("#ffffff"),
    },
    active: {
      alpha: [
        1,
        {
          duration: 250,
          easing: 'ease-in-out',
        },
      ]
    },
    unactive: {
      alpha: [
        0,
        {
          duration: 250,
          easing: 'ease-in-out',
        },
      ]
    },
  } satisfies IntrinsicNodeStyleProps,
};
