import { Text, View } from "@lightningjs/solid";
import styles from "./Detail.style";
import { movieDetailState, setFocusMenu } from "../../state";
import { Column, Row } from "@lightningjs/solid-primitives";
import DetailMore from "./DetailMore";
const basePath = import.meta.env.BASE_URL;

export default function DetailMovie(props) {
  function focusDetail() {
    console.log(props.data)
  }

  const displayRuntime = (data) => {
    const hours = Math.floor(data / 60);
    const minutes = data % 60;
    return `${hours}h ${minutes}m`
  }

  return (
    <View
      style={styles.Wrapper}
      onFocus={focusDetail}
      autofocus
      plinko onLeft={() => {
        setFocusMenu(true);
      }}
    >
      <Column style={styles.Main} skipFocus plinko>
        <Text style={styles.Title} skipFocus forwardStates>{props.data.title}</Text>
        <View style={styles.Section}>
          <View style={styles.Rate} skipFocus forwardStates>
            <View src={basePath + 'assets/star-active.png'} style={styles.RateStar}></View>
            <View src={basePath + 'assets/star-active.png'} style={styles.RateStar}></View>
            <View src={basePath + 'assets/star-active.png'} style={styles.RateStar}></View>
            <View src={basePath + 'assets/star.png'} style={styles.RateStar}></View>
            <View src={basePath + 'assets/star.png'} style={styles.RateStar}></View>
          </View>
          <View style={styles.Quantity} skipFocus forwardStates>
            {props.data.heroContent.badges.map((quantity, index) => (
              <Text key={index} style={styles.QuantityItem}>{quantity}</Text>
            ))}
          </View>
          <Text style={styles.QuantityItem} skipFocus forwardStates>{displayRuntime(props.data.runtime)}</Text>
        </View>
        <Row style={styles.Control} skipFocus plinko>
          <View style={styles.ControlItem} skipFocus><Text style={styles.ControlItemText}>Play</Text></View>
          <View style={styles.ControlItem} skipFocus><Text style={styles.ControlItemText}>Add</Text></View>
        </Row>
        <View style={styles.OverView}>
          <Text style={styles.OverViewText} skipFocus forwardStates>{props.data.overview}</Text>
        </View>
      </Column>
      <DetailMore />
    </View>
  );
}
