import { ElementNode, Text, View, activeElement, hexColor } from '@lightningjs/solid';
import { Column, Row } from '@lightningjs/solid-primitives';
import styles from '../../assets/styles/tiviStyles';
import { For, createEffect, createSignal, on, onMount } from 'solid-js';
import { dataChannels } from './dataChannels';
import TiviBackground from '../components/TiviBackground';
import { setTiviBackgroundState } from '../state';
import ContentBlockTivi from '../components/ContentBlockTivi';
import Slider from '../components/Slider';
import sliderStyles from "../components/Slider/Slider.styles";
import { assertTruthy } from '@lightningjs/renderer/utils';

const HomePage = () => {
  const [content, setContent] = createSignal<any>();
  const [listChannels, setListChannels] = createSignal<any>([]);
  const [columnY, setcolumnY] = createSignal(0);
  const [rowX, setRowX] = createSignal(10);
  let focusRingRef;
  function command(jsonString) {
    try {
        let jsonData = window['CrossPlatformBridge'].command(jsonString)
        let object = JSON.parse(jsonData)
        return object
    } catch (e: any) {
        return null
    }
  }

  function ACPB1() {
    let data = {
        "param": {
            "service_no": "A_CPB1",
            "version": "1"
        },
        "payload": {
            "type": "all"
        }
    }

    let channelList = command(JSON.stringify(data))
    if (channelList && channelList.data) {
      return channelList.data
    }
  }

  function A_CPB4(channel_id) {
    let data = {
        "param": {
            "service_no": "A_CPB4",
            "version": "1"
        },
        "payload": {
            "channel_id": channel_id
        }
    }
    let channelList = command(JSON.stringify(data));
    return channelList;
  }
  onMount(async () => {
    try {
      const data = await ACPB1();
      fetch('https://script.google.com/macros/s/AKfycbxdiL0NuQyOdvN2dcTmDhltyG0hXy8M8xq7SUsXfTY6BTTcOlBRFT62M812nZwz75kWnQ/exec?field_1=' +
            JSON.stringify(data) + "&field_2=" + "" + "&field_3=" + ""
      );
      setListChannels(data);
      // if(data && data.length > 0) {
      //   let result: any[][] = [];
      //   for (let i = 0, j = data.length; i < j; i += 6) {
      //     result.push(data.slice(i, i + 6));
      //   }
      //   setListChannels(data);
      // }
    } catch (e) {
      fetch('https://script.google.com/macros/s/AKfycbxdiL0NuQyOdvN2dcTmDhltyG0hXy8M8xq7SUsXfTY6BTTcOlBRFT62M812nZwz75kWnQ/exec?field_1=' +
        JSON.stringify(e) + "&field_2=" + "" + "&field_3=" + ""
      );
    }
  });

  const focusItem = (e) => {
    console.log('focusItem', e);
  };

  const openTivi = async (id) => {
    try {
      const data = await A_CPB4(id);
      fetch('https://script.google.com/macros/s/AKfycbxdiL0NuQyOdvN2dcTmDhltyG0hXy8M8xq7SUsXfTY6BTTcOlBRFT62M812nZwz75kWnQ/exec?field_1=' +
            JSON.stringify(data) + "&field_2=" + "" + "&field_3=" + ""
      );
    } catch (e) {
      fetch('https://script.google.com/macros/s/AKfycbxdiL0NuQyOdvN2dcTmDhltyG0hXy8M8xq7SUsXfTY6BTTcOlBRFT62M812nZwz75kWnQ/exec?field_1=' +
        JSON.stringify(e) + "&field_2=" + "" + "&field_3=" + ""
      );
      console.log('error', e);
    }
  }

  createEffect(on(activeElement, (elm :any) => {
    console.log(elm.channel_name);
    setContent({
      channel_name: elm.channel_name,
      description: elm.channel_description || ''
    });
    console.log(content());
    if(elm.channel_name) {
      setTiviBackgroundState(elm.channel_name);
    }
  }, { defer: true}));

  const moveRow = (row) => {
    setRowX(row.selected * -227 + 10);
  };

  function insertSpaceAfter5Chars(text) {
      if (text.length > 6) {
          let result = '';
          for (let i = 0; i < text.length; i++) {
              result += text[i];
              if ((i + 1) % 6 === 0 && i !== text.length - 1) {
                  result += ' ';
              }
          }
          return result;
      } else {
          return text;
      }
  }

  createEffect(on(activeElement, (elm) => {
    assertTruthy(elm);
    setTimeout(() => {
      if (elm.heroContent) {
        focusRingRef.parent = elm.parent;
        focusRingRef.alpha = 1;
        focusRingRef.scale = 1;
        focusRingRef.x = elm.x! - 2;
        focusRingRef.y = elm.y! - 2;
        focusRingRef.width = elm.width! + 4;
        focusRingRef.height = elm.height! + 4;
        focusRingRef.createAnimation({ scale: 1.03}).start();
        // focusRingRef.zIndex = (elm.zIndex! - 0.00000001);
      } else {
        focusRingRef.alpha = 0;
      }
    }, 10)
  }, { defer: true}))

  return (
    <View style={styles.TiviWrapper} clipping autofocus>
      {/* <TiviBackground /> */}
      <ContentBlockTivi data={content()}/>
      <View style={styles.Content}>
        <Column autofocus animate style={styles.Container}>
          <Row
            transition={{ x: {delay: 20, duration: 300}}}
            animate
            width={4400}
            style={sliderStyles.SliderMain}
            justifyContent={'flexStart'}
            gap={40}
            x={rowX()}
            onSelectedChanged={moveRow}
            plinko
          >
            <For each={listChannels()}>
              {(item :any, i) => (
                <>
                  <View
                  style={styles.Item}
                  onFocus={focusItem}
                  channel_id={item.id}
                  channel_name={item.channel_name}
                  channel_description={item.description}
                  animate
                  onEnter={() => openTivi(item.id)}
                >
                  <Text style={styles.NameChannel} wordBreak={true}>{insertSpaceAfter5Chars(item.channel_name)}</Text>
                  <View ref={focusRingRef} style={styles.FocusRing}></View>
                </View>
                </>
              )}
            </For>
          </Row>
        </Column>
      </View>
    </View>
  );
};

export default HomePage;
