import { ElementNode, View, activeElement } from '@lightningjs/solid';
import { Column, Row } from '@lightningjs/solid-primitives';
import Title from '../components/Title';
import styles from '../../assets/styles/homeStyles';
import popularApi from "../api/providers/popular";
import { Show, createEffect, createMemo, createSignal, on } from 'solid-js';
import { createInfiniteScroll } from '../components/pagination';
import Slider from '../components/Slider';
import ContentBlock from '../components/ContentBlock';
import HomeBackground from '../components/HomeBackground';
import { setHomeBackgroundState } from '../state';
import Loading from '../components/Loading';

const HomePage = () => {
  const [heroContent, setHeroContent] = createSignal({});
  const [rowXTrending, setRowXTrending] = createSignal(10);
  const [rowXPopular, setRowXPopular] = createSignal(10);
  const [rowXRecommend, setRowXRecommend] = createSignal(10);
  const [columnY, setcolumnY] = createSignal(0);

  const popularProvider = createMemo(() => {
    return createInfiniteScroll(popularApi(1));
  });

  const moveRowPopular = (row) => {
    row.children.selected?.setFocus();
    setRowXPopular(row.selected * -227 + 10);
    if([].concat.apply([], popularProvider().pages()).length - row.selected < 15) {
      popularProvider().setPage(p => p + 1);
    }
  };

  const moveRowTrending = (row) => {
    setRowXTrending(row.selected * -227 + 10);
    if([].concat.apply([], popularProvider().pages()).length - row.selected < 15) {
      popularProvider().setPage(p => p + 1);
    }
  };

  const moveRowRecommend = (row) => {
    setRowXRecommend(row.selected * -227 + 10);
    if([].concat.apply([], popularProvider().pages()).length - row.selected < 15) {
      popularProvider().setPage(p => p + 1);
    }
  };


  createEffect(on(activeElement, (elm :any) => {
    console.log(elm);
    if (elm.backdrop) {
      setHomeBackgroundState(`https://image.tmdb.org/t/p/original/${elm.data.backdrop_path}`);
    }

    if (elm.heroContent) {
      setHeroContent(elm.heroContent);
    }
  }, { defer: true}));

  function onRowFocus(this: ElementNode) {
    this.children.selected?.setFocus();
    setcolumnY((this.y || 0) * -1 + 24);
  }

  function onRowTrendingFocus(this: ElementNode) {
    this.children.selected?.setFocus();
    setcolumnY((this.y || 0) * -1 + 24);
  }

  function onRowRecommendFocus(this: ElementNode) {
    this.children.selected?.setFocus();
    setcolumnY((this.y || 0) * -1 + 24);
  }

  return (
    <View style={styles.HomeWrapper}>
      <Show when={popularProvider().pages().length == 0}>
        <Loading />
      </Show>
      <HomeBackground />
      <ContentBlock y={360} x={20} {...heroContent()}></ContentBlock>
      <View style={styles.HomeMain} clipping autofocus>
        <Column style={styles.HomeList} clipping plinko y={columnY()} animate>
          <Row style={styles.HomeItem} onFocus={onRowFocus} autofocus>
            <Title title={'Popular'} icon={'assets/popular.png'}></Title>
            <Slider
              data={[].concat.apply([], popularProvider().pages())}
              rowX={rowXPopular}
              moveRow={moveRowPopular}
              autofocus={true}
            ></Slider>
          </Row>
          <Row style={styles.HomeItem} onFocus={onRowTrendingFocus}>
            <Title title={'Trending'} icon={'assets/trending.png'}></Title>
            <Slider
              data={[].concat.apply([], popularProvider().pages())}
              rowX={rowXTrending}
              moveRow={moveRowTrending}
            ></Slider>
          </Row>
          <Row style={styles.HomeItem} onFocus={onRowRecommendFocus}>
            <Title title={'Recommend'} icon={'assets/recommend.png'}></Title>
            <Slider
              data={[].concat.apply([], popularProvider().pages())}
              rowX={rowXRecommend}
              moveRow={moveRowRecommend}
            ></Slider>
          </Row>
        </Column>
      </View>
    </View>
  );
};

export default HomePage;
