import { createEffect, createMemo, on } from "solid-js";
import NoticeItem from "./Item"
import { createInfiniteScroll } from "../pagination";
import { page1, page2 } from "./mock.js";
import { chunkArray } from "../../api/formatters/ItemFormatter";
import { View, Text } from "@lightningjs/solid";

export default function Notice(props) {

  function fakeAPi(page) {
    return new Promise(resolve => {
        setTimeout(() => {
            let fakeData;
            if (page === 1) {
              fakeData = {
                page: 1,
                results: page1,
                total_pages: 2,
                total_results: 40
              }
            } else {
              fakeData = {
                page: 2,
                results: page2,
                total_pages: 2,
                total_results: 40
              }
            }
            resolve(fakeData);
        }, 0);
    });
  }

  function mockData(size?: number) {
    return (pageIndex: number): Promise<any> => {

      const sizePage = size ? size : 20;
      let result = fakeAPi(pageIndex).then((item: any) => {
        let results = item.results;
        let chunks = chunkArray(results, sizePage);
        return chunks;
      });
  
      return result;
    }
  }

  const provider = createMemo(() => {
    return createInfiniteScroll(mockData());
  });

  function moveChange(item) {
    if([].concat.apply([], provider().pages()).length - item.selected < 4) {
      provider().setPage(p => p + 1);
    }
  }

  return (
    <NoticeItem {...props} items={[].concat.apply([], provider().pages())} onSelectedChanged={moveChange} />
  );
}
