import { View, Text, For } from '@lightningjs/solid';
import styles from "./Slider.styles";
import { Row } from '@lightningjs/solid-primitives';
import Thumbnail from '../Thumnail';
import { createSelector } from 'solid-js';

export default function Slider(props) {
  const isFirst = createSelector(() => {
    return 0;
  });

  return (
    <Row
      transition={{ x: {delay: 20, duration: 300}}}
      animate
      width={4400}
      style={styles.SliderMain}
      justifyContent={'flexStart'}
      gap={40}
      x={props.rowX()}
      onSelectedChanged={props.moveRow}
      plinko
    >
      <For each={props.data}>
        {(items :any, i) => <Thumbnail {...items} autofocus={props.autofocus && isFirst(i())}/>}
      </For>
    </Row>
  );
}
