import { useLocation, useNavigate } from "@solidjs/router";
import { View, activeElement } from "@lightningjs/solid";
import { useFocusManager, useAnnouncer } from "@lightningjs/solid-primitives";
import Background from "../components/Background";
import Menu from "../components/Menu/Menu";
import Notice from "../components/Notice"
import { Show, createEffect, createSignal, on, onMount } from "solid-js";
import styles from "../styles";
import { focusMenu, setFocusMenu, setOpenMenuBG, openNotify } from "../state";

const App = (props) => {
  const announcer = useAnnouncer();
  announcer.enabled = false;
  const navigate = useNavigate();
  const location: any = useLocation();
  let navDrawer, lastFocused, notifyRef;
  const [localtionActive, setLocaltionActive] = createSignal(location);
  const [loaded, setLoaded] = createSignal(false);

  onMount(async () => {
    setLoaded(true);
  });

  useFocusManager({
    Menu: "m",
    Escape: "Escape",
  });

  onMount(() => {
    setFocusMenu(true);
  });

  createEffect(() => {
    setLocaltionActive(location.pathname);
  });

  createEffect(on(openNotify, (elm) => {
    if (elm) {
      return navDrawer.states.has("focus") && notifyRef.setFocus()
    } else {
      navDrawer.setFocus();
      setOpenMenuBG(true);
    }
  }, { defer: true}))

  return (
    <Show
      when={loaded()}
    >
      <View
        ref={window.APP}
        onLast={() => history.back()}
        onMenu={() => navigate("/")}
        style={{ width: 1920, height: 1080 }}
        onLeft={() => {
          if (
            navDrawer.states.has("focus") ||
            (focusMenu() == false && (localtionActive() == "/search" || localtionActive().includes('/movie/')))
          ) {
            return false;
          }
          var tempActive = activeElement();
          if (!tempActive?.states.has("not-set")) {
            lastFocused = tempActive;
          }
          navDrawer.setFocus();
          setOpenMenuBG(true);
        }}
        onRight={() => {
          setFocusMenu(false);
          setOpenMenuBG(false);
          return navDrawer.states.has("focus") && lastFocused.setFocus()
        }}
        plinko
      >
        <Background />
        <View style={styles.pageWrapper}>{props.children}</View>
        <Menu ref={navDrawer} />
        <Notice ref={notifyRef}/>
      </View>
    </Show>
  );
};

export default App;
