import {
  View,
  Text,
  ElementNode,
  hexColor,
} from "@lightningjs/solid";
import {
  menuIconStyle,
} from "../../../../assets/styles/menuStyle";
import { Column, Row } from "@lightningjs/solid-primitives";
import styles from "./../Notice.styles";
import { For, Show, createEffect, createSignal, on } from 'solid-js';
import { setOpenNotify } from "../../../state";
import theme from 'theme';
const basePath = import.meta.env.BASE_URL;

export default function NoticeItem(props) {
  let backdrop, menuWrapperRef;
  const [columnY, setcolumnY] = createSignal(60);
  
  function onFocus(this: ElementNode) {
    this.states!.add("active");
    this.children.forEach((c: any) => {
      c.states!.add("active");
    });
    this.children.selected!.setFocus();
    menuWrapperRef.children.forEach((c) => c.states!.add("active"));
    menuWrapperRef.states!.add("active");
    menuWrapperRef.states!.remove("unactive");
  }

  function onBlur(this: ElementNode) {
    this.states!.add("unactive");
    menuWrapperRef.states!.add("unactive");
    menuWrapperRef.states!.remove("active");
    menuWrapperRef.children.forEach((c) => c.states!.remove("active"));
    this.children.forEach((c) => c.states!.remove("active"));
    this.children.forEach((c) => c.states!.add("unactive"));
    this.children.forEach((c) => c.states!.add("not-set"));
  }

  function onLeft(this: ElementNode) {
    setOpenNotify(false);
    this.children.forEach((c) => c.states!.add("not-set"));
  }

  function handleFocus(this: ElementNode) {
    if ((this.y || 0) > theme.layout.screenH / 2) {
      setcolumnY(80 + ((this.y || 0) - theme.layout.screenH / 2) * -1)
      if (this.parent) {
        this.parent.children.forEach((c: any) => {
          if (!c.states.has("active")) {
            c.states!.add("active");
          }
        });
      }
    }
  }

  return (
    <View style={styles.MenuWrapper} ref={menuWrapperRef}>
      <View style={styles.NoticeWrapper}>
        <View src={basePath + `assets/icon-back.png`} y={5} width={40} height={40}></View>
        <View display="flex" justifyContent="center" width={360}>
          <Text fontSize={30} x={40} y={10}>Notifications {props.len}</Text>
        </View>
      </View>
      <Column
        {...props}
        onFocus={onFocus}
        onBlur={onBlur}
        style={styles.Column}
        y={columnY()}
        onLeft={onLeft}
        animate
        plinko
        key={props.len}
      >
        <For each={props?.items}>
        {(item, i) => (
          <View
            style={menuIconStyle}
            forwardStates
            animate
            onFocus={handleFocus}
            id={item.path}
          >
            <View
              src={item?.image || basePath + "assets/default-thumbnail.jpg"}
              style={styles.NotifyImage}
            ></View>
            <Text style={styles.MenuText}>
              {item.name}
            </Text>
            <Text style={styles.NoticeDesc}>
              {item.desc}
            </Text>
          </View>
        )}
        </For>
      </Column>
      <View ref={backdrop}></View>
    </View>
  );
}
