import { IntrinsicNodeStyleProps, hexColor } from "@lightningjs/solid";
import theme from 'theme';

export default {
  LoadingWrapper: {
    x: 0,
    y: 0,
    width: window.devicePixelRatio > 1 ? 1820 : window.innerWidth - 100,
    height: window.devicePixelRatio > 1 ? 1080 : window.innerHeight,
    color: hexColor("#000000"),
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    zIndex: 999,
  } satisfies IntrinsicNodeStyleProps,
  LoadingMain: {
    display: "flex",
    flexDirection: "row",
    gap: 30,
    height: 30
  } satisfies IntrinsicNodeStyleProps,
  LoadingItem: {
    x: 0,
    y: 0,
    width: 15,
    height: 15,
    color: hexColor("#ffffff"),
    borderRadius: 7.5,
    choose: {
      transition: {
        duration: 0.5,
        timingFunction: "ease-in-out"
      },
      color: hexColor("#ff0000"),
      scale: 1.3
    }
  } satisfies IntrinsicNodeStyleProps,
};
