import { View, hexColor } from '@lightningjs/solid';
import styles from '../../assets/styles/detailStyles';
import DetailMovie from '../components/DetailMovie';
import { useParams, useLocation } from "@solidjs/router";
import { Show, createEffect, createResource, on } from 'solid-js';
import * as provider from '../api/providers/entity';

export default function DetailPage() {
  const params = useParams();
  const location = useLocation();
  const [data] = createResource(() => ({...params}), provider.getInfo);

  createEffect(on(data, (data) => {
    console.log(location)
    console.log(data);
  }, { defer: true}))
  
  return (
    <>
      <Show when={data()?.backdrop_path}>
        <View
          style={styles.ImageBackground}
          zIndex={-5}
          src={`https://image.tmdb.org/t/p/original/${data()?.backdrop_path}`}
          skipFocus
        ></View>
        <View
          alpha={0.97}
          style={styles.DetailMain}
          animate
          effects={{
            radialGradient: {
              colors: [0x0000007C, 0x0000007C, hexColor("#131313")],
              stops: [1.5, 0.1, 1.3],
              height: 700,
              width: window.innerWidth - 500,
              pivot: [0.9, -0.1], 
            },
          }}
          skipFocus
        ></View>
        <DetailMovie data={data()} autofocus/>
      </Show>
    </>
  );
}
