import { View, Text, hexColor } from '@lightningjs/solid';
import { withPadding } from '@lightningjs/solid-primitives';
import theme from 'theme';
withPadding;

const blockWidth = 1100;
const HeadlineStyles = {
  ...theme.typography.display2,
  width: blockWidth,
  height: 58,
  contain: 'both',
};
const Headline = (props) => (
  <Text {...props} style={HeadlineStyles}>
    {props.children}
  </Text>
);

const DescriptionStyles = {
  ...theme.typography.body1,
  width: blockWidth,
  height: 72,
  contain: 'both',
};

const Description = (props) => (
  <Text {...props} style={DescriptionStyles}>
    {props.children}
  </Text>
);


const ContentBlockTivi = (props) => {
  return <View
      {...props}
      style={{
        ...theme.flexColumn,
        width: blockWidth,
        height: 160,
        gap: 16,
        x: 50,
        y: 50
      }}
    >
      <Headline>{props.data?.channel_name}</Headline>
      <Description>{props.data?.description}</Description>
    </View>;
};

export default ContentBlockTivi;
