const VITE_API_KEY_V4="e9e9d8da18ae29fc430845952232787c";
const VITE_REQUEST_TOKEN="596e02522af6fe40f284e6de58fd0865f2cbaae3";
const API_BASE = 'https://api.themoviedb.org/3';
let tmdbConfig;
let baseImageUrl;
const basePosterSize = 'w185';

const defaultFetchParams = {
  headers: {
    'Content-Type': 'application/json',
    Authorization: 'Bearer ' + VITE_REQUEST_TOKEN,
  },
};

export function getImageUrl(path: string, posterSize: string = basePosterSize) {
  return baseImageUrl + posterSize + path;
}

function get(path: string, params: RequestInit = {}) {
  if (tmdbConfig) {
    return _get(path, params);
  } else {
    return loadConfig().then(() => _get(path, params))
  }
}

function _get(path: string, params: RequestInit = {}) {
  return fetch(API_BASE + path + (path.includes('?') ? '&' : '?') + 'api_key=' + VITE_API_KEY_V4, {
    ...defaultFetchParams,
    ...params,
  }).then((r) => r.json());
}

function loadConfig() {
  return _get('/configuration').then((data) => {
    tmdbConfig = data;
    baseImageUrl = data.images?.secure_base_url;
    return data;
  });
}

export default {
  get,
  loadConfig,
};
