import { createEffect, createMemo, on, createSignal,
  Show, createSelector, For } from "solid-js";
import { ElementNode, View, activeElement } from '@lightningjs/solid';
import { Row } from '@lightningjs/solid-primitives';
import { useNavigate, useParams } from "@solidjs/router";
import styles from '../styles';
import { setGlobalBackground } from "../state";
import browseProvider from '../api/providers/browse';
import { createInfiniteScroll } from '../components/pagination';
import ContentBlock from "../components/ContentBlock";
import { assertTruthy } from "@lightningjs/renderer/utils";
import Thumbnail from "../components/Thumnail";

const Browse = () => {
  const params = useParams();
  const [heroContent, setHeroContent] = createSignal({});
  const navigate = useNavigate();
  let sliderMain;
  const isFirst = createSelector(() => {
    return 0;
  });

  const provider = createMemo(() => {
    return createInfiniteScroll(browseProvider(params.filter || 'all', 20));
  });
  createEffect(on(activeElement, (elm) => {
    if (elm.backdrop) {
      setGlobalBackground(elm.backdrop);
    }
    if (elm.heroContent) {
      setHeroContent(elm.heroContent);
    }
    
  }, { defer: true}))

  function onEnter(this: ElementNode) {
    let entity = this.children.selected;
    assertTruthy(entity && entity.href);
    navigate(entity.href)
  };

  const [rowX, setRowX] = createSignal(90);

  function moveRow(row) {
    setRowX(row.selected * -227 + 90);
    if([].concat.apply([], provider().pages()).length - row.selected < 9) {
      provider().setPage(p => p + 1);
    }
  }

  return (
    <Show when={provider().pages().length} ref={sliderMain}>
      <View>
        <ContentBlock y={200} x={90} {...heroContent()}></ContentBlock>
        <Row onSelectedChanged={moveRow} onEnter={onEnter}
          transition={{ x: {delay: 20, duration: 300}}} animate
          x={rowX()} y={450} width={4400} style={styles.Row} justifyContent={'flexStart'} gap={40}>
          <For each={[].concat.apply([], provider().pages())}>
            {(items :any, i) => <Thumbnail {...items} autofocus={isFirst(i())}/>}
          </For>
        </Row>
      </View>
    </Show>
  );
};

export default Browse;
