import {
  createEffect,
  createMemo,
  on,
  createSignal,
  Show,
  createResource,
  createSelector,
  For,
  onMount,
} from "solid-js";
import { ElementNode, View, activeElement, Text } from "@lightningjs/solid";
import { Column } from "@lightningjs/solid-primitives";
import { useNavigate, useParams } from "@solidjs/router";
import { Button, TileRow } from "../components";
import styles from "../styles";
import { setGlobalBackground } from "../state";
import browseProvider from "../api/providers/browse";
import { createInfiniteScroll } from "../components/pagination";
import ContentBlock from "../components/ContentBlock";
import * as entityProvider from "../api/providers/entity";
import { assertTruthy } from "@lightningjs/renderer/utils";
import ListColumn from "../components/ListColumn";
import ListSlider from "../components/ListSlider";

import theme from "theme";
import { setState, state } from "../store";

const Browse = () => {
  const params = useParams();
  const [columnY, setcolumnY] = createSignal(0);
  const [entityInfo, setEntityInfo] = createSignal();
  const [entityData] = createResource(entityInfo, entityProvider.getInfo);
  const [heroContent, setHeroContent] = createSignal({});
  const navigate = useNavigate();


  const isFirst = createSelector(() => {
    return 0;
  });
  let navDrawer, lastFocused, buttonNoti;

  const provider = createMemo(() => {
    return createInfiniteScroll(browseProvider(params.filter || "all", 8));
  });
  createEffect(
    on(
      activeElement,
      (elm) => {
        if (elm.backdrop) {
          setGlobalBackground(elm.backdrop);
        }
        // if (elm.entityInfo) {
        //   setEntityInfo(elm.entityInfo);
        // }
        if (elm.heroContent) {
          setHeroContent(elm.heroContent);
        }
      },
      { defer: true }
    )
  );


  function onRowFocus(this: ElementNode) {
    this.children.selected?.setFocus();
    setcolumnY((this.y || 0) * -1 + 24);
    let numPages = provider().pages().length;
    if (this.parent) {
      if (
        numPages === 0 ||
        (this.parent.selected && this.parent.selected >= numPages - 2)
      ) {
        provider().setPage((p) => p + 1);
      }
    }
  }

  function onEnter(this: ElementNode) {
    let entity = this.children.selected;
    assertTruthy(entity && entity.href);
    navigate(entity.href);
  }

  return (
    <View>
      {/* <View y={100} >fgfgfgfgf {state.count}</View> */}
      <Show when={provider().pages().length}>
        <ContentBlock y={150} x={90} {...heroContent()}></ContentBlock>
        <ListColumn
          type="sliderList"
          onEnter={onEnter}
          onFocus={onRowFocus}
          data={provider().pages()}
          y={columnY()}
          autofocus={true}
        />
        {/* <ListSlider x={90} y={450} onEnter={onEnter} data={[...provider().pages()[0]]}/> */}
      </Show>
     
      
    </View>
  );
};

export default Browse;
