import { View, Text, activeElement } from '@lightningjs/solid';
import styles from '../../assets/styles/loadingStyles';
import { createEffect, createSignal, on, onMount } from 'solid-js';

export default function Loading(props) {
  let LoadingMainRef;
  const [itemFocus, setItemFocus] = createSignal(0);
  const [direction, setDirection] = createSignal(true);

  onMount(() => {
    setInterval(() => {
     
      if(itemFocus() == 0) {
        setDirection(true);
      } 
      if(itemFocus() == 5) {
        setDirection(false);
      }
      if(direction()) {
        if(itemFocus() > 0) {
          LoadingMainRef.children[itemFocus()].states.remove('choose');
        }
        LoadingMainRef.children[itemFocus()].states.add('choose');
        setItemFocus(prev => prev + 1);
      } else {
        setItemFocus(prev => prev - 1);
        LoadingMainRef.children[itemFocus()].states.remove('choose');
      }
    }, 200)
  })

  return (
    <View forwardStates animate style={styles.LoadingWrapper} autofocus>
      <View style={styles.LoadingMain} ref={LoadingMainRef} skipFocus>
        <View style={styles.LoadingItem} skipFocus animate></View>
        <View style={styles.LoadingItem} skipFocus animate></View>
        <View style={styles.LoadingItem} skipFocus animate></View>
        <View style={styles.LoadingItem} skipFocus animate></View>
        <View style={styles.LoadingItem} skipFocus animate></View>
      </View>
    </View>
  );
}
