import { ElementNode, Text, View } from '@lightningjs/solid';
import { Column, Row } from '@lightningjs/solid-primitives';
import styles from '../../assets/styles/settingStyles';
import Title from '../components/Title';
import { createSignal } from 'solid-js';

export default function SettingPage() {
  const [language, setLanguage] = createSignal<any>("JP");

  function focusRowSettingLanguage(this :ElementNode) {
    // console.log(this);
    // this.children[1].setFocus();
    this.setFocus();
  }

  function focusItem(this :ElementNode) {
    console.log(this.value);
    this.setFocus();
    if(this.value) {
      setLanguage(this.value);
    }
   
  }

  return (
    <View style={styles.Wrapper}>
      {/* <Column style={styles.Main} autofocus plinko>
        <Title title={"Setting"} skipFocus></Title>
        <Row style={styles.Select} onFocus={focusRowSettingLanguage} plinko>
          <View style={styles.SelectTitle} skipFocus>
            <Text style={styles.SelectTitleContent}>Language: </Text>
          </View>
          <View style={styles.Option} forwardStates onFocus={focusItem} value={"JP"}>
            <View style={styles.OptionIcon} forwardStates animate>
              <View style={styles.OptionCenterIcon} animate></View>
            </View>
            <Text style={styles.OptionTitle} skipFocus>JP</Text>
          </View>
          <View style={styles.Option} forwardStates onFocus={focusItem} value={"EN"}>
            <View style={styles.OptionIcon} forwardStates animate>
              <View style={styles.OptionCenterIcon} animate></View>
            </View>
            <Text style={styles.OptionTitle} skipFocus>EN</Text>
          </View>
        </Row>
        <View style={styles.btnSave} animate><Text style={styles.btnSaveText}>Save</Text></View>
      </Column> */}
    </View>
  );
}
