import { IntrinsicNodeStyleProps, hexColor } from "@lightningjs/solid";

export default {
  Wrapper: {
    height: 400,
    width: window.innerWidth - 100,
  } satisfies IntrinsicNodeStyleProps,
  Title: {
    color: hexColor("#ffffff"),
    fontSize: 20,
    width: window.innerWidth / 2,
    alpha: 0.6,
  } satisfies IntrinsicNodeStyleProps,
  Tabs: {
    display: "flex",
    flexDirection: "row",
    gap: 20,
    height: 30,
  } satisfies IntrinsicNodeStyleProps,
};
