import api from '..';
import { convertItemsToTiles, chunkArray } from '../formatters/ItemFormatter';

let cache = new Map();
const leftoverTiles = new Map();

export default function (size: number) {
  const filter = '';
  return async (pageIndex: number): Promise<any> => {
    const url = `/movie/popular?language=en-US&page=${pageIndex}`;
    if (cache.has(url)) {
      return cache.get(url);
    }

    const sizePage = size;

    let result = await api.get(url).then((trending) => {
      let results = trending.results.filter((r) => !r.adult);
      let tiles = (leftoverTiles.has(filter) ? leftoverTiles.get(filter) : []).concat(convertItemsToTiles(results));
      let chunks = chunkArray(tiles, sizePage);
      if (chunks[chunks.length - 1].length < sizePage) {
        leftoverTiles.set(filter, chunks.pop());
      } else {
        leftoverTiles.delete(filter);
      }
      return chunks;
    });
    return result;
  }
}
