function Logic1 () {
	'use strict';

	// Hide controls in case of no mouse or touch activity for 10 seconds
	var videoControls = document.getElementById('video-controls'),
		video = document.getElementById('video'),
		player = document.getElementById('player');

	// Make mouse out function for nested children
	function makeMouseOutFn(elem) {
		var list = traverseChildren(elem);
		return function onMouseOut(event) {
			var e = event.toElement || event.relatedTarget;
			if (!!~list.indexOf(e)) {
				return;
			}
			isOnPlayer = false;
			hideControls();
		};
	}

	function hideControls() {
		if (!video.paused) {
			// videoControls.classList.remove('display-control');
		}
	};

	// Utility function for traverse children nodes
	function traverseChildren(elem) {
		var children = [];
		var q = [];
		q.push(elem);
		while (q.length > 0) {
			var elem = q.pop();
			children.push(elem);
			pushAll(elem.children);
		}
		function pushAll(elemArray) {
			for (var i = 0; i < elemArray.length; i++) {
				q.push(elemArray[i]);
			}
		}
		return children;
	};

};

function Logic2 () {
	'use strict';

	var player = document.getElementById('player'),
		video = document.getElementById('video'),
		videoControls = document.getElementById('video-controls'),
		playpause = document.getElementById('playpause'),
		mute = document.getElementById('mute'),
		fullScreen = document.getElementById('max'),
		backward = document.getElementById('back'),
		forward = document.getElementById('next'),
		currentTime = document.getElementById('current-time'),
		remainTime = document.getElementById('end-time'),
		progress = document.getElementById('progress'),
		progressBar = document.getElementById('progress-bar');
	
	var	videoSource = video.getAttribute('data-src');
	var numType = 0;
	video.controls = false;

	// HLS video init
	if (Hls.isSupported()) {
		var hls = new Hls();
		hls.loadSource(videoSource);
		hls.attachMedia(video);
		hls.on(Hls.Events.MANIFEST_PARSED, function () {
			// NOTICE: 1
			video.hidden = false;
			video.play();
			initVideoQualityOptions(hls);
			setTimeout(() => {
				playpause.focus()
			}, 500);
		});
		hls.on(Hls.Events.LEVEL_LOADED, function (event, data) {
			if (numType === 0) {
				(data.details || {}).live ? numType = 1 : numType = 2;
				if (numType === 1) {
					changeControlsToLive();
				}
			}
		});
	}

	// Add events for buttons
	playpause.addEventListener('click', function (e) {
		if (video.paused || video.ended) video.play();
		else video.pause();
	});

	mute.addEventListener('click', function (e) {
		video.muted = !video.muted;
		changeButtonState('mute');
	});

	// React to the user clicking within the progress bar
	progress.addEventListener('click', function (e) {
		var pos = (e.pageX - (this.offsetLeft + this.offsetParent.offsetLeft + player.offsetLeft)) / this.offsetWidth;
		video.currentTime = pos * video.duration;
		currentTime.innerHTML = secondsToTime(video.currentTime)
	});

    function enterFullscreen() {
        const element = document.documentElement;

        if (element.requestFullscreen) {
            element.requestFullscreen();
        } else if (element.mozRequestFullScreen) {
            element.mozRequestFullScreen();
        } else if (element.webkitRequestFullscreen) {
            element.webkitRequestFullscreen();
        } else if (element.msRequestFullscreen) {
            element.msRequestFullscreen();
        }
    }

    function exitFullscreen() {
        if (document.exitFullscreen) {
            document.exitFullscreen();
        } else if (document.mozCancelFullScreen) {
            document.mozCancelFullScreen();
        } else if (document.webkitExitFullscreen) {
            document.webkitExitFullscreen();
        } else if (document.msExitFullscreen) {
            document.msExitFullscreen();
        }
    }

	// handle full screen function 
	function toggleFullScreen() {
		var currPlayer = document.getElementById('player');
		var isMobile = window.orientation > -1;
		if (!(/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent))) {
			if (document.fullscreenElement) {
				exitFullscreen();
			} else {
				enterFullscreen();
			}
		}
		// offsetWidth offsetHeight
		var width = window.innerWidth
			|| document.documentElement.clientWidth
			|| document.body.clientWidth;

		var height = window.innerHeight
			|| document.documentElement.clientHeight
			|| document.body.clientHeight;
		if (width - currPlayer.offsetWidth > 40 ||  height - currPlayer.offsetHeight > 40) {
			currPlayer.style.width = '100%';
			currPlayer.style.top = '0';
		} else {
			currPlayer.style.width = '80%';
			currPlayer.style.top = '20px';
		}
	}

	fullScreen.addEventListener('click', function (event) {
		toggleFullScreen();
	});

	backward.addEventListener('click', function (event) {
		video.currentTime = Math.max(video.currentTime - 10, 0);
		currentTime.innerHTML = secondsToTime(video.currentTime);
	});

	forward.addEventListener('click', function (event) {
		video.currentTime = Math.min(video.currentTime + 10, video.duration);
		currentTime.innerHTML = secondsToTime(video.currentTime);
	});

	// Wait for the video's meta data to be loaded, then set the progress bar's max value to the duration of the video
	video.addEventListener('loadedmetadata', function () {
        // Calculate new time, but don't exceed video duration
        var newTime = Math.min(video.currentTime, video.duration);
        video.currentTime = newTime;
		currentTime.innerHTML = secondsToTime(video.currentTime);
		remainTime.innerHTML = secondsToTime(video.duration);
	});

	// Add event listeners for video specific events
	video.addEventListener('play', function () {
		changeButtonState('playpause');
	}, false);
	video.addEventListener('pause', function () {
		changeButtonState('playpause');
	}, false);

	// As the video is playing, update the progress bar
	video.addEventListener('timeupdate', function () {
		if (!progress.getAttribute('max')) progress.setAttribute('max', video.duration);
		progress.value = video.currentTime;
		currentTime.innerHTML = secondsToTime(video.currentTime);
		progressBar.style.width = Math.floor((video.currentTime / video.duration) * 100) + '%';
	});

	// Changes the button state of certain button's so the correct visuals can be displayed with CSS
	function changeButtonState(type) {
		// Play/Pause button
		if (type == 'playpause') {
			if (video.paused || video.ended) {
				playpause.setAttribute('data-state', 'play');
				videoControls.classList.add('display-control');
			}
			else {
				playpause.setAttribute('data-state', 'pause');
			}
		}
		// Mute button
		else if (type == 'mute') {
			mute.setAttribute('data-state', video.muted ? 'unmute' : 'mute');
		}
	};
};


function initVideoQualityOptions(hls) {

	var qualityBtn = document.getElementById('quality-btn');
	var quality = document.getElementById('quality');
	qualityBtn.addEventListener('click', function namedListener (event) {
		makeActive();
		quality.classList.toggle("show");
	});

	//Create and append the options
	var qualities = hls.levels;
	while (quality.firstChild) {
		quality.removeChild(quality.firstChild);
	}
	var qAuto = document.createElement("button");
	qAuto.setAttribute("data-value", -1);
	qAuto.setAttribute("class", "quality");
	qAuto.setAttribute("tabindex", "0");
	qAuto.setAttribute("id", "quality-0");

	qAuto.textContent = 'Auto';
	quality.appendChild(qAuto);
	for (var i = 0; i < qualities.length; i++) {
		var q = document.createElement("button");
		q.setAttribute("data-value", i);
		q.setAttribute("class", "quality");
		q.setAttribute("tabindex", "0");
		q.setAttribute("id", "quality-" + (i + 1));
		q.textContent = (qualities[i].name || qualities[i].height) + 'p';
		quality.appendChild(q);
	}

	// update dropdown selection
	var elems = document.querySelectorAll('#quality button');

	function makeActive(UserSelectionEvent) {
		for (var i = 0; i < elems.length; i++)
			{
				if(!UserSelectionEvent && i == hls.currentLevel + 1) {
					if (hls.autoLevelEnabled) {
						elems[0].classList.add('active');
						elems[i].classList.remove('active');
					} else {
						elems[i].classList.add('active');
					}
				} else{
					elems[i].classList.remove('active');
				}
			}
			if(UserSelectionEvent){
				var q_level = parseInt(this.getAttribute('data-value'));
				hls.currentLevel = q_level;
				this.classList.add('active');
			}
	};

	for (var i = 0; i < elems.length; i++)
		elems[i].addEventListener('click', makeActive);
};

function secondsToTime(seconds) {
	var hours = Math.floor(seconds / 3600);
	var minutes = Math.floor((seconds % 3600) / 60);
	var remainingSeconds = Math.floor(seconds % 60);

	var timeString = "";

	if (hours > 0) {
		timeString += `${hours.toString().padStart(2, '0')}:`;
	}

	timeString += `${minutes.toString().padStart(2, '0')}:${remainingSeconds.toString().padStart(2, '0')}`;

	return timeString;
}

export function playVideo(url) {
	var src = url || "https://test-streams.mux.dev/x36xhzz/x36xhzz.m3u8";
	var video = document.getElementById('video');
	video.setAttribute('data-src', src);
	initApp();
}

export function closeVideo() {
	var video = document.getElementById('video');
	video.hidden = true;
	video.pause();
	changeControlsToVideo();
	var quality = document.getElementById('quality');
	quality.className = "dropdown-content";
	replaceDOM();
}


function replaceDOM() {
	var old_element = document.getElementById("player");
	old_element.parentNode.removeChild(old_element);
	setTimeout(() => {
		createHtml();
	}, 10);
}

function changeControlsToLive() {
	var time = document.getElementById('time'),
		live = document.getElementById('live-indicator');
	time.className = "display_none";
	live.className = "";
	live.setAttribute('data-type', 'live');
}

export function backwardTime() {
	var video = document.getElementById('video'),
	live = document.getElementById('live-indicator'),
	currentTime = document.getElementById('current-time');
	var attrType = live.getAttribute('data-type');
	if (attrType === 'video') {
		video.currentTime = Math.max(video.currentTime - 10, 0);
		currentTime.innerHTML = secondsToTime(video.currentTime);
	}
}

export function forwardTime() {
	var video = document.getElementById('video'),
	live = document.getElementById('live-indicator'),
	currentTime = document.getElementById('current-time');
	var attrType = live.getAttribute('data-type');
	if (attrType === 'video') {
		video.currentTime = Math.min(video.currentTime + 10, video.duration);
		currentTime.innerHTML = secondsToTime(video.currentTime);
	}
}

export function upControls() {
	var activeID = document.activeElement.getAttribute('id');
	if (!activeID) {
		var playpause = document.getElementById('playpause');
		playpause.focus();
		return;
	}
	if (activeID === 'playpause') {
		var playpause = document.getElementById('max');
		playpause.focus();
		return;
	}
	if (activeID === 'max') {
		var playpause = document.getElementById('mute');
		playpause.focus();
		return;
	}
	if (activeID === 'mute') {
		var playpause = document.getElementById('quality-btn');
		playpause.focus();
		return;
	}
	if (activeID === 'quality-btn') {
		var quality = document.getElementById('quality');
		if (quality.className === "dropdown-content show") {
			var qualityDetail = document.getElementById('quality-0');
			if (qualityDetail) {
				qualityDetail.focus();
			}
		}
		return;
	}
	if (activeID.includes('quality-')) {
		const num = parseInt(extractNumber(activeID), 10);
		var quality = document.getElementById('quality-' + (num + 1));
		if (quality) {
			quality.focus();
		}
		return;
	}
}

export function downControls() {
	var activeID = document.activeElement.getAttribute('id');
	if (!activeID) {
		var playpause = document.getElementById('playpause');
		playpause.focus();
		return;
	}
	if (activeID === 'max') {
		var playpause = document.getElementById('playpause');
		playpause.focus();
		return;
	}
	if (activeID === 'mute') {
		var playpause = document.getElementById('max');
		playpause.focus();
		return;
	}
	if (activeID === 'quality-btn') {
		var playpause = document.getElementById('mute');
		playpause.focus();
		return;
	}
	if (activeID.includes('quality-')) {
		const num = parseInt(extractNumber(activeID), 10);
		if (num === 0) {
			var quality = document.getElementById('quality-btn');
			quality.focus();
			return;
		} else {
			var quality = document.getElementById('quality-' + (num - 1));
			quality.focus();
			return;
		}
	}
}

function extractNumber(str) {
    var matches = str.match(/quality-(\d+)/);

    if (matches && matches[1]) {
        return parseInt(matches[1], 10);
    } else {
        return null; // or any default value you prefer
    }
}

function changeControlsToVideo() {
	var time = document.getElementById('time'),
		live = document.getElementById('live-indicator');
	live.className = "display_none";
	time.className = "";
	live.setAttribute('data-type', 'video');
}

function initApp() {
	Logic1();
	Logic2();
}
function createHtml () {
	var htmlString = '<video id="video" tabindex="0" poster="data:image/png;base64," hidden></video><div id="video-controls" class="controls display-control"><button id="playpause" tabindex="0" type="button" data-state="play"></button><button id="back" tabindex="0" type="button" data-state="back"></button><button id="next" tabindex="0" type="button" data-state="next"></button><button id="max" tabindex="0" type="button" data-state="max"></button><button id="mute" type="button" data-state="mute">Mute/Unmute</button><div class="dropdown"><button id="quality-btn" class="dropbtn btn-settings"></button><div id="quality" class="dropdown-content"></div></div><div data-type="video" id="live-indicator" class="display_none"><span id="red-dot"></span><span id="live-text">Live</span></div><div id="time"><span id="current-time">10:11</span>/<span id="end-time">11:23</span></div><div class="progress"><progress id="progress" tabindex="0" value="0" min="0"><span id="progress-bar"></span></progress></div></div>'
	var app = document.getElementById("app");
	var div = document.createElement('div');
	div.id = "player"
	div.innerHTML = htmlString.trim();
	app.parentNode.insertBefore(div, app.nextSibling)
}
document.addEventListener('DOMContentLoaded', createHtml);